import React, { Component } from "react";
import { connect } from "react-redux";
import { Field, reduxForm } from "redux-form";
import {
  ReanderField,
} from "../../../components/helpers/field";
// import CetakKeuanganKas from "./CetakKeuanganKas";

class FormInportDataPusat extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }
  render() {
    return (
      <div className="row">
        <div className="col-lg-6">
          <Field
            name="file_inport"
            component={ReanderField}
            type="file"
            label="Import Data"
          />
        </div>
        <div className="col-lg-6">
          <div className="text-right">
            <label>&nbsp;</label>
            <button
              className="btn btn-primary btn-block"
            >
              Kirim 
            </button>
          </div>
        </div>
      </div>
    );
  }
}

FormInportDataPusat = reduxForm({
  form: "FormInportDataPusat",
  enableReinitialize: true,
})(FormInportDataPusat);
export default connect()(FormInportDataPusat);
